body {
  margin: 0;
  padding: 0;
  font-family: 'Courier New', Courier;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(89deg, #276CA1 0%, #0A0A61 100%);
}

button{
  cursor: pointer;
}

.Tilt {
	background: linear-gradient(89deg, #276CA1 0%, #0A0A61 100%);
}
.form{
	width: 700px;
}
.bounding-box{
	position:absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
}
.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}



